<form class="contact" ref="form" @submit.prevent="sendEmail"><PostIt class="toast-post-it" :text="postItText" :class="postItClass" :destroyable="false"></PostIt><div class="header">Send me a message!</div><div class="row"><label>Name:</label><input v-model="name" type="text" name="name"></div><div class="row"><label>Email:</label><input v-model="email" type="email" name="email"></div><div class="row"><label>Message:</label><textarea v-model="message" name="message"></textarea></div><div class="row"><input class="button" type="submit" :disabled="disabled" value="Send"></div></form>